import React, { Component } from 'react'
import {IMG_PATH} from '../constants/Config'
export class NowPlaying extends Component {
    render() {
        return (
            <div className='nowplaying'>
                <h1>now playing</h1>
                <p>55 Items on the list</p>
                <div className="nowplaying_bg">
                    <div className='pos'>
                    <img src={`${IMG_PATH}/Coloringbook.png`}  className='rounded-circle img-fluid'/>
                    <div className='roundspace'></div></div>
                <div class="range-slider">
	<input class="input-range" type="range"/>

</div>
<div className='timeduration'>
    <span>2:10</span>
    <span>-0.3:10</span>
</div>
<div className='musiciconwrap'>
    <span><i class="fas fa-random"></i></span>
    <span><i class="fas fa-backward"></i></span>
    <span><i class="fas fa-pause"></i></span>
    <span><i class="fas fa-forward"></i></span>
    <span><i class="fas fa-redo"></i></span>
</div>
                </div>
                
            </div>
        )
    }
}

export default NowPlaying
