import React from 'react'
import {Container,Row,Col} from 'react-bootstrap'
import MostPopular from './MostPopular'
import NowPlaying from './NowPlaying'
function Most() {
    return (
        <Container className='spctop'>
  <Row>
    <Col sm={12} lg={8}><MostPopular/></Col>
    <Col sm={12} lg={4}><NowPlaying/></Col>
  </Row>
</Container>
    )
}

export default Most
