import React from 'react'
import {Container, Row, Col} from 'react-bootstrap'
import Leftnav from './Components/Leftnav'

function MinWrap() {
    return (
        <Container fluid>
  <Row>
    <Col sm={4}><Leftnav/></Col>
    <Col sm={8}>sm=8</Col>
  </Row>
</Container>
    )
}

export default MinWrap
