import React from 'react';
import logo from './logo.svg';
import './App.css';
import MinWrap from './MinWrap';
import {Route, Switch} from 'react-router-dom'
import Leftnav from './Components/Leftnav';
import {Container,Row,Col} from 'react-bootstrap'
import Search from './Components/Search';
import Home from './Pages/Home';
import Browser from './Pages/Browser';
import Album from './Pages/Album';
import Artist from './Pages/Artist';
import Vedio from './Pages/Vedio';
import RecentlyPlayed from './Pages/RecentlyPlayed'
import Localfile from './Pages/LocalFiles'

function App() {
  return (
    <>
      <Container fluid>
  <Row>
    <Col sm={3} className="element"><Leftnav/></Col>
    <Col sm={9}>
      <Search/>
      <Switch>
      <Route exact path='/' component={Home}></Route> 
         <Route exact path='/Music-app' component={Home}></Route>    
         <Route exact path='/Music-app/browser' component={Browser}></Route>    
         <Route exact path='/Music-app/album' component={Album}></Route>    
         <Route exact path='/Music-app/artist' component={Artist}></Route>    
         <Route exact path='/Music-app/vedio' component={Vedio}></Route>    
         <Route exact path='/Music-app/recentlyplayed' component={RecentlyPlayed}></Route>    
         <Route exact path='/Music-app/localfile' component={Localfile}></Route>
         <Route path="*" component={()=> {return "404 Page Note Found"}}  ></Route>  
         </Switch></Col>
      </Row>
      </Container>
    </>
  );
}

export default App;
