import React from 'react'
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import {IMG_PATH} from '../constants/Config'
function Courselslide() {
    const responsive = {
        superLargeDesktop: {
          // the naming can be any, depends on you.
          breakpoint: { max: 4000, min: 3000 },
          items: 5
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 4
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };
      
    return (
        <div className='courhead'>
            <h1>Billboard TopChart</h1>
            <Carousel responsive={responsive} itemClass="carousel-item-padding-40-px" infinite={true}>
        <div className='jss100'>
            <img src={`${IMG_PATH}/Coloringbook.png`}/>
                <h2>Coloring Book</h2>
        <p>Pop King</p>
        </div>
        <div className='jss100'><img src={`${IMG_PATH}/blueneighbourhood.png`}/>
        <h2>Blue Neighbourhood</h2>
        <p>Troy Sivan</p>
        </div>
        <div className='jss100'><img src={`${IMG_PATH}/Starboy.png`}/>
        <h2>StarBoy</h2>
        <p>The Weekend</p>
        </div>
        <div className='jss100'><img src={`${IMG_PATH}/mirage.png`}/>
        <h2>Mirage</h2>
        <p>Else Twin</p>
        </div>
        <div className='jss100'><img src={`${IMG_PATH}/Alive.png`}/>
        <h2>Alive</h2>
        <p>Pop King</p>
        </div>
        <div className='jss100'><img src={`${IMG_PATH}/mirage.png`}/>
        <h2>Mirage</h2>
        <p>Else Twin</p>
        </div>
        <div className='jss100'><img src={`${IMG_PATH}/blueneighbourhood.png`}/>
        <h2>Blue Neighbourhood</h2>
        <p>Troy Sivan</p>
        </div>
        <div className='jss100'><img src={`${IMG_PATH}/mirage.png`}/>
        <h2>Mirage</h2>
        <p>Pop King</p>
        </div>
      </Carousel>
        </div>
    )
}

export default Courselslide
