import React from 'react'
import {Container,Row,Col, Button} from 'react-bootstrap'
import { Link } from 'react-router-dom'
import {IMG_PATH} from '../constants/Config'
function Search() {
    return (
        <Container className="pad">
  <Row>
    <Col xs={12} md={8} className="sesrchwraper">
        <input type="text" maxlength= "12" placeholder="Search..." className="searchbar sesrchwraper"/>
        <img src={`${IMG_PATH}/searchicon.png`}  alt="searchicon" className="button"/>
  </Col>
    <Col xs={12} md={4}>
     <div className='topicon'>
     <ul>
      <Link><li><i class="far fa-bell"></i></li></Link>   
      <Link><li><i class="fas fa-cog"></i></li></Link>   
      <Link><li><Button variant="primary">Upgrade Plan</Button>{' '}</li></Link>   
     </ul>
     </div>   
    </Col>
    </Row>
</Container>
    )
}

export default Search
