import React from 'react'
import {Container,Row,Col} from 'react-bootstrap'
import {Link} from 'react-router-dom'
import {Route, Switch} from 'react-router-dom'
import {IMG_PATH} from '../constants/Config'
function Leftnav() {
    return (
        <Container className=''>
  <Row>
      <div className='lftnavbar col-12'>
    <Col xs={4} md={4} className="m-auto">
    <img src={`${IMG_PATH}/profile.jpg`} className='rounded-circle img-fluid'/>
     </Col>
    <h1>Farzan Faruk</h1>
    <p>Luova.studio7@gmail.com</p>
    </div>
    <div className='menuwrap col-12'>
    <nav>
     <ul>
        <Link to='/Music-app'><li><i className="fas fa-home"></i>Home</li></Link>
         <Link to='/Music-app/browser'><li><i class="fas fa-window-restore"></i>Browse</li></Link>
         <Link to='/Music-app/album'><li><i class="far fa-images"></i>Album</li></Link>
         <Link to='/Music-app/artist'><li><i class="fas fa-stamp"></i>Artist</li></Link>
         <Link to='/Music-app/vedio'><li><i class="fas fa-video"></i>Vedio</li></Link>
     </ul>   
    </nav>
    <h2>My Music</h2>
    <nav>
     <ul>
        <Link to='/Music-app/recentlyplayed'><li><i className="fas fa-history"></i>Recently Played</li></Link>
         <Link to='//Music-app/localfile'><li><i class="far fa-file-image"></i>Local Files</li></Link>
        </ul>   
    </nav>
    </div>
  </Row>
</Container>
    )
}

export default Leftnav
